.payment-container {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  padding: 2rem;
}

.payment-card {
  background-color: #1a1f3b;
  border-radius: 16px;
  padding: 2rem;
  max-width: 500px;
  width: 100%;
  box-shadow: 0 8px 20px rgba(123, 47, 247, 0.2);
  text-align: center;
}

.payment-title {
  font-size: 2rem;
  margin-bottom: 1rem;
  color: #00f5d4;
}

.payment-description {
  font-size: 1rem;
  margin-bottom: 2rem;
  color: #eaeaea;
}

.license-options {
  margin-bottom: 2rem;
}

.license-option {
  background: linear-gradient(135deg, #7b2ff7, #00f5d4);
  padding: 1.5rem;
  border-radius: 12px;
  color: #0a0f1e;
  font-weight: bold;
  margin-bottom: 1rem;
  box-shadow: 0 4px 12px rgba(0, 245, 212, 0.5);
  transition: transform 0.2s ease-in-out;
}

/* Color del título y descripción dentro del botón */
.license-option h2,
.license-option p {
  color: #ffffff; /* Color de texto blanco para mejor contraste */
  margin: 0;
}

.license-option:hover {
  transform: translateY(-5px);
  cursor: pointer;
  background: linear-gradient(135deg, #00f5d4, #7b2ff7);
  color: #ffffff;
}

.wallet-container {
  margin-top: 1rem;
}

.loading-text {
  color: #f2e96b;
  font-style: italic;
}
