/*footer*/
.footer {
    background-color: #0a0f1e;
    color: #eaeaea;
    padding: 2rem 1rem 1rem 1rem;
    margin-top: 2rem;
    box-shadow: 0 -4px 12px rgba(0, 245, 212, 0.2);
}

.footer-content {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    gap: 2rem;
    max-width: 1200px;
    margin: 0 auto;
}

.footer-section {
    flex: 1;
    min-width: 200px;
}

.footer-title {
    color: #00f5d4;
    font-size: 1.5rem;
    margin-bottom: 0.5rem;
}

.footer-subtitle {
    color: #7b2ff7;
    font-size: 1.2rem;
    margin-bottom: 0.5rem;
}

.footer-text {
    font-size: 0.9rem;
    line-height: 1.5;
}

.footer-links {
    list-style: none;
    padding: 0;
}

.footer-links li {
    margin: 0.3rem 0;
}

.footer-links a {
    text-decoration: none;
    color: #eaeaea;
    transition: color 0.3s ease;
}

.footer-links a:hover {
    color: #f2e96b;
}

.social-icons a {
    margin-right: 10px;
    font-size: 1.4rem;
    text-decoration: none;
    color: #00f5d4;
    transition: transform 0.2s ease-in-out;
}

.social-icons a:hover {
    transform: scale(1.2);
    color: #7b2ff7;
}

.footer-bottom {
    margin-top: 1rem;
    text-align: center;
    font-size: 0.8rem;
    border-top: 1px solid rgba(255, 255, 255, 0.1);
    padding-top: 0.5rem;
    color: #eaeaea;
}

